.footer {
    margin-top: 10px;
    width: 100%;
      display: flex;
      justify-content: center;
     
  }
  .footer__wrapper {
      position: absolute !important;
      bottom: 0 !important;
    font-family: sans-serif;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    letter-spacing: 0.5px;
    font-size: 18px;
    width: 100%;
    height: 70px;
      background-color: #2D2F34;
      box-sizing: border-box;
      border-radius: 1px;
      border: solid 2.1px #404040;
      font-weight: bold;
      line-height: 24px;
      -moz-user-select: none;
      -khtml-user-select: none;
      user-select: none; 
  }
  @media (max-width: 979px){
    .footer__wrapper{
        width: 479px;
        height: 50px;
    }
  @media (max-width: 499px){
    .footer__wrapper{
      width: 354px;
      height: 50px;
  }
  }
    .footer__wrapper>*{
      font-size: 10px;
    }
  }
  
  .footer__developers{
  display: flex;
  justify-content: space-around;
  width: 100%;
  height: 100;
  }
  
  
  .developer{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  .developer__links{
    width: 100px;
    display: flex;
    justify-content: space-between;
  }
  @media (max-width: 979px){
    .developer__links{
      width: 60px;
    }
  }
  
  .footer__school-logo-container {
    display: flex;
      flex-direction: column;
      align-items: center;
  }
  .footer__school-logo{
    fill:#adadad;
    width: 100px;
    height: 40px;
  }
  .footer__school-logo:hover{
    transition: 0.3s;
    fill:#2493f2;
  }
  @media (max-width: 979px){
    .footer__school-logo{
      width: 50px;
    height: 20px;
    }
    }
  .ico{
    fill: #5c5c5c;
  
  }
  .ico>*{
    fill: #5c5c5c;
    width: 25px;
    height: 25px;
  }
  .ico>*:hover{
    transition: 0.3s;
    fill: #2493f2;
  }
  @media (max-width: 979px){
    .ico>*{
      fill: #5c5c5c;
      width: 15px;
      height: 15px;
    }
  }
  @media (max-width: 499px) {
    .chart__icon_full-screen{
      visibility: hidden;
    }
    .chart__icons-full-screen{
      visibility: hidden;
    }
  }